    getList(){
        Axios.ajax({
            url:signBaseUrl+'/SSOService.asmx/GetArticleList',
            type:'post',
            data:{
                isShowLoading:false,
                params: {
                    page:1,
                    limit: 10
                }
            }
        }).then((res)=>{
            this.setState({
                item:res.Data.ds
            })
        }).catch((res)=>{

        })
    }
    handleScroll() {
        var top = document.documentElement.scrollTop || document.body.scrollTop;
        if (top > 0) {
            this.props.menuShowBool({
                'isShow': 'block'
            })
        } else {
            this.props.menuShowBool({
                'isShow': 'none'
            })
        }
    }
    LogOut(){
        var that=this;
        confirm({
            title: '',
            content: '',
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk() {
                sessionStorage.removeItem('userInfo');
                that.props.LogOutProps({
                    isLogIn:false,
                    UserName:''
                })
                const form = document.createElement('form');
                form.id = 'form-file-download';
                form.name = 'form-file-download';
                //  body 
                document.body.appendChild(form);
                const input = document.createElement('input');
                input.type = 'hidden';
                input.name = 'SoftList';
                input.value = JSON.stringify(that.props.userInfo.SoftList);
                form.appendChild(input);
                const input2 = document.createElement('input');
                input2.type = 'hidden';
                input2.name = 'znUrl';
                input2.value = "http://"+window.location.host+"/#/home";
                form.appendChild(input2);
                // form 
                form.method = 'POST';
                // form 
                form.action =localBaseUrl+'/ClearCookie.aspx';
                form.submit();
                document.body.removeChild(form);
            },
            onCancel() {
                console.log('Cancel');
            },
        });

    }
