import React from 'react'
import './index.less';
import {connect} from 'react-redux'
import {switch_menuShowBool, switchIndex} from '../../redux/action/index'
import {Link} from 'react-router-dom'
import rightImg from '../../assets/img/help_banner.png';
import conn from '../../assets/img/conn.png';
import ewm from '../../assets/img/ewm.png';
import utils from '../../utils/utils'
import {Tabs,Modal} from 'antd';
const TabPane = Tabs.TabPane;
class Help extends React.Component {
    constructor(props) {
        super(props);
        this.submit = this.submit.bind(this)
        this.inputChange = this.inputChange.bind(this)
        this.inputFocus = this.inputFocus.bind(this)
        this.initIndex = this.initIndex.bind(this)
        this.callback = this.callback.bind(this)

    }

    componentDidMount() {
        this.props.menuShowBool({
            'isShow': 'block'
        });
        this.props.menuInit("5");
        window.scrollTo(0, 0);
        this.initIndex()
    }
    initIndex(){

        const { search } = this.props.location;
        const paramsString = search.substring(1);
        if(paramsString){
            const searchParams = new URLSearchParams(paramsString);
            const index = searchParams.get('key');
            this.setState({key: index})
        }
    }
    state={
        znTelephone:"",
        znEmail:"",
        znQq:"",
        znTextArea:"",
        errorInfo:"",
        key:'1'
    }
    submit(){
        if (utils.trim(this.state.znTextArea) =='') {
            this.setState({
                errorInfo: ""
            });
            return;
        }
        var that=this;
        Modal.success({
            title: '',
            content: (
                <div>
                    <p>!</p>
                </div>
            ),
            onOk() {
                that.props.history.push("/home")
            },
        });
    }
    inputChange(e, type) {
        var value = e.target.value;
        this.setState({
            [type]: value
        });
    }
    callback(key) {
        this.setState({
            key:key
        })
    }
    inputFocus() {
        this.setState({
            errorInfo: ''
        })
    }
    render() {

        return (
            <div className="help-wrap">
                <div className="contnet">
                    <div className="btn"></div>
                    <img src={rightImg} alt=""/>
                </div>
                <div className="tab_box">
                    <Tabs activeKey={this.state.key} onChange={this.callback}>
                        <TabPane tab="" key="1">
                            <div className="tap">
                                <div className="tab_item">
                                    <h4></h4>
                                    <a></a>
                                </div>
                                <div className="tab_item">
                                    <h4></h4>
                                    <a></a>
                                    <a></a>
                                    <a></a>
                                    <a></a>
                                </div>
                                <div className="tab_item">
                                    <h4></h4>
                                    <a></a>
                                    <a></a>
                                    <a>iSCADA</a>
                                </div>
                            </div>
                        </TabPane>
                        <TabPane tab="" key="2">
                            <div className="tap">
                                <div className="tab_item">
                                    <h4></h4>
                                </div>
                                <div className="tab_item">
                                    <h4></h4>
                                </div>

                            </div>
                        </TabPane>
                        <TabPane tab="" key="3">
                            <div className="tap tap2">
                                <h2> </h2>
                                <h3></h3>
                                <h3></h3>

                                <textarea className="textarea" value={this.state.znTextArea} onChange={(e) => {
                                    this.inputChange(e, "znTextArea")
                                }} onFocus={() => {
                                    this.inputFocus()
                                }} ></textarea>

                                <h3></h3>
                                <input type="text" onFocus={() => {
                                    this.inputFocus()
                                }}  onChange={(e) => {
                                    this.inputChange(e, "znTelephone")
                                }} value={this.state.znTelephone} maxLength="30" placeholder="" className="input"/>
                                <input type="text" onFocus={() => {
                                    this.inputFocus()
                                }}  onChange={(e) => {
                                    this.inputChange(e, "znQq")
                                }}   value={this.state.znQq} maxLength="30"placeholder="QQ/" className="input"/>
                                <input type="text" onFocus={() => {
                                    this.inputFocus()
                                }}  onChange={(e) => {
                                    this.inputChange(e, "znEmail")
                                }}  value={this.state.znEmail} maxLength="30" placeholder="" className="input input0"/>
                                <div className="errorInfo">{this.state.errorInfo}</div>
                                <div className="t_j" onClick={this.submit}></div>
                                <Link to="/home" className="q_x"></Link>
                            </div>

                        </TabPane>
                        <TabPane tab="" key="4">
                            <div className="conn_box">
                                <img src={conn} alt=""/>
                                <ul>
                                    <li></li>
                                    <li>0451-88006777</li>
                                    <li>0451-86811680</li>
                                    <li>17</li>
                                </ul>
                                <img className="ewm" src={ewm} alt=""/>
                            </div>
                        </TabPane>
                    </Tabs>
                </div>
            </div>
        );
    }
}
const mapStateToProps = state => {
    return {}
}
const mapDispatchToProps = dispatch => {
    return {
        menuShowBool: function (obj) {
            dispatch(switch_menuShowBool(obj))
        },
        menuInit: function (num) {
            dispatch(switchIndex(num))
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Help)